<template>
   <div class="top-home">
    <div class="space-betweens">
      <h2>Instagram</h2>
      <div>
        <img
          src="https://raw.githubusercontent.com/namssu/test/main/img/icon-like.png"
        />
        <img
          src="https://raw.githubusercontent.com/namssu/test/main/img/icon-send.png"
        />
      </div>
    </div>
  </div>

  <div class="story-profiles f-d-center">
    <div class="my-profile f-center-center">+</div>
    <div>
      <div class="profile-box">
        <img
          src="https://images.unsplash.com/photo-1587463272361-565200f82b33?q=80&w=1887&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D"
        />
      </div>
      <p>namssu</p>
    </div>

    <div v-for="(_homepostsData, i) in homepostsData" :key="i">
      <div class="story-active"></div>
      <div class="profile-box">
        <img :src="homepostsData[i].userprofile" />
      </div>
      <p>{{ homepostsData[i].user }}</p>
    </div>
  </div>

  <div
    class="post-content"
    v-for="(_homepostsData, i) in homepostsData"
    :key="i"
  >
    <div class="post-account space-betweens">
      <div class="f-d-center">
        <div class="profile-box">
          <img :src="homepostsData[i].userprofile" />
        </div>
        <div>
          <b>{{ homepostsData[i].user }}</b>
        </div>
      </div>

      <span class="material-icons-outlined md-36"> more_vert </span>
    </div>

    <div class="post-box">
      <img :src="homepostsData[i].img" />
    </div>

    <div class="post-option space-betweens">
      <div class="f-d-center">
        <div class="f-d-center">
          <img
            src="https://raw.githubusercontent.com/namssu/test/main/img/icon-like-red.png"
          />
          <p>{{ homepostsData[i].like }}</p>
        </div>
        <div class="f-d-center">
          <img
            src="https://raw.githubusercontent.com/namssu/test/main/img/icon-comment.png"
          />
          <p>{{ homepostsData[i].commentnum }}</p>
        </div>
        <img
          src="https://raw.githubusercontent.com/namssu/test/main/img/icon-send.png"
          @click="modal1 = true"
          class="cursor-pointer"
        />
      </div>
      <img
        src="https://raw.githubusercontent.com/namssu/test/main/img/icon-save.png"
      />
    </div>

    <div class="post-likedetail f-d-center">
      <div class="profile-box">
        <img
          src="https://images.unsplash.com/photo-1587463272361-565200f82b33?q=80&w=1887&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D"
        />
      </div>
      <p>Liked by <b>namssu</b> and <b>others</b></p>
    </div>

    <div class="post-text">
      <div>
        <p>
          <b>{{ homepostsData[i].user }}</b> {{ homepostsData[i].text }}
        </p>
      </div>

      <div>
        <div class="space-betweens">
          <p>
            <b>{{ homepostsData[i].commentuser }}</b>
            {{ homepostsData[i].commenttext }}
          </p>
          <img
            src="https://raw.githubusercontent.com/namssu/test/main/img/icon-like.png"
          />
        </div>
        <div class="space-betweens commentreply">
          <p>
            <b>{{ homepostsData[i].user }} </b>
            <span class="blue-text"> @{{ homepostsData[i].commentuser }} </span>
            {{ homepostsData[i].commentreply }}
          </p>
          <img
            src="https://raw.githubusercontent.com/namssu/test/main/img/icon-like.png"
          />
        </div>
      </div>

      <div class="f-d-center">
        <p>
          <span class="gray-text">{{ homepostsData[i].date }} • </span>
          <b>See translation</b>
        </p>
      </div>
    </div>
  </div>

  <div class="back-black" v-if="modal1 == true">
    <div class="back-white">
      <div class="space-betweens options">
        <div>
          <div class="circle-option f-center-center">
            <span class="material-icons-outlined">share</span>
          </div>
          <p class="">공유하기</p>
        </div>

        <div>
          <div class="circle-option f-center-center">
            <span class="material-icons-outlined">alternate_email</span>
          </div>
          <p>Threads</p>
        </div>

        <div>
          <div class="circle-option f-center-center">
            <span class="material-icons-outlined">link</span>
          </div>
          <p>링크 복사</p>
        </div>

        <div>
          <div class="circle-option f-center-center">
            <span class="material-icons-outlined">sms</span>
          </div>
          <p>SMS</p>
        </div>
      </div>
      <button @click="modal1 = false" type="button" class="btn">close</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeCom",
  props: {
    homepostsData: Object,
  },
  data() {
    return {
      modal1: false,
    };
  },
};
</script>

