import { createWebHistory, createRouter } from "vue-router";
import Home from "./components/Home";
import Account from "./components/Account";

const routes = [
  {
    path: "/",
    component: Home,
  },
  {
    path: "/Account",
    component: Account,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
